import React from 'react'
import convertPythonDictToJSON from './Converter'


const Profile = ({data}) => {
    data = JSON.parse(convertPythonDictToJSON(data))
    console.log(data)
    console.log(typeof(data))
    const first_name = data['"student"']['"first_name"'].replaceAll('"','')
    const second_name = data['"student"']['"second_name"'].replaceAll('"','')
    const mail = data['"student"']['"mail"'].replaceAll('"','')
    const university = data['"student"']['"university"'].replaceAll('"','')
    return (
        <div className='profile' style={{'margin-top':'100px'}}>
            <h2><span>Name</span>: <strong>{first_name}</strong></h2>
            <h2><span>Surname</span>: <strong>{second_name}</strong></h2>
            <h2><span>Mail</span>: <strong>{mail}</strong></h2>
            <h2><span>University</span>: <strong>{university}</strong></h2>
            <div className='col-md-6 col-sm-12'>
                <div className='row'>
                    <div className="col-md-6">
                        <div className="border-rounded-button">
                            <a href="/logout">Log Out</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Profile
